import Api from '@/components/service/api'
import Auth from '@/components/service/auth'


export default {
  name: "Player",

  components: {
  },

  methods: {
    async check() {
      let response = null

      try {
        response = await Api.get('?get=client');
        Auth.setSession(response.get.token);
      } catch (error) {
        console.log('error: ', error);
        Auth.invalidateSession()
        this.$router.push('/login');
        return
      }

      console.log(response);
    },

    async dislike() {
      try {
        await Api.get('?action=dislike');
      } catch (error) {
        return
      }
    },

    async pause() {
      try {
        await Api.get('?action=pause');
      } catch (error) {
        return
      }

      if (this.state == 'Paused') {
        this.state = 'Playing';
      } else {
        this.state = 'Paused';
      }
    },

    async play() {
      try {
        await Api.get('?action=play');
      } catch (error) {
        return
      }

      this.get_track()
    },

    async like() {
      try {
        await Api.get('?action=like');
      } catch (error) {
        return
      }
    },

    async next() {
      try {
        await Api.get('?action=next');
      } catch (error) {
        return
      }

      this.playPercent = 0;
      this.get_track()
    },

    async get_track() {
      let response = null;

      try {
        response = await Api.get('?get=track');
      } catch (error) {
        return
      }

      this.track = response.get.track;
      this.cover = `data:image/png;base64, ${this.track.cover}`;
    },

    async get_player() {
      let response = null;

      try {
        response = await Api.get('?get=player_state');
      } catch (error) {
        return
      }

      this.player = response.get.player_state;
      this.duration = this.player.track_duration
      this.playInkrement = 0.5 / ((this.duration / 100) * 0.001);
      this.playPercent = Number((this.player.current_time * (100 / this.player.track_duration)).toFixed(2));
      this.state = this.player.state;
    },

    inkrement_progressbar() {
      if (this.state != 'Playing') {
        return;
      }

      this.playPercent = Number((Number(this.playPercent) + Number(this.playInkrement)).toFixed(2));

      if (this.playPercent > 100) {
        this.get_track()
      }
    },
  },

  created() {
    this.playPercent = 0;
    this.state = '';
    this.cover = '';
    this.check();
    this.get_track();
    this.get_player();

    let timerId = setInterval(() => this.get_player(), 10000);
    let timerId2 = setInterval(() => this.inkrement_progressbar(), 500);
  },

  data() {
    return {
      track: this.track,
      cover: this.cover,
      playPercent: this.playPercent,
    };
  },
};



